<style lang="scss" scope>
.item_reset{
    width: 180px;
}
</style>
<template>
    <el-dialog :title="title" :visible.sync="addDialogVisible" width="35%">
        <div class="ui-box leave_dialog">
            <el-form :model="userItem" label-width="80px" :rules="formRule" ref="formUser">
                <el-form-item label="名称" prop="username">
                    <el-input size="mini" class="item_reset" v-model="userItem.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input size="mini" type="password" show-password class="item_reset" v-model="userItem.password"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input size="mini" class="item_reset" v-model="userItem.email"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="phone">
                    <el-input size="mini" class="item_reset" v-model="userItem.phone"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-select placeholder="请选择" size="mini" class="item_reset" v-model="userItem.sex">
                        <el-option label="男" value="1"></el-option>
                        <el-option label="女" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="年龄">
                    <el-input size="mini" class="item_reset" v-model="userItem.age"></el-input>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select placeholder="请选择" size="mini" class="item_reset" v-model="userItem.status">
                        <el-option label="禁用" value="0"></el-option>
                        <el-option label="启用" value="1"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false" size="mini">取 消</el-button>
            <el-button type="primary" @click="submit" size="mini">确 定</el-button>
        </span>
    </el-dialog>
</template>
<script>
export default {
    name: 'addItem',
    props: {
        userItem:{}
    },
    data(){
        return{
            formRule:{
                username: [{required:true,message:'请输入名称',trigger: 'blur'}],
                // password: [{trigger: 'blur',required:true}],
                password:[{ required: true, message: '密码不能为空', trigger: 'blur' },
                    { min: 4, max: 16, message: '密码长度在 4 到 16 个字符', trigger: 'blur' },
                    {pattern:/^(?=.*[0-9])(?=.*[a-zA-Z]).{4,16}$/,message:'请输入正确的密码格式:字母开头+数字'}
            ],
                phone: [{pattern:/^1[3456789]\d{9}$/,message:'请输入正确的电话格式'}],
                email: [{pattern:/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,message:'请输入正确的邮箱格式',trigger: 'blur'}],
            },
            addDialogVisible: false,
            title: '',
        }
    },
    methods:{
        submit(){
            this.$refs['formUser'].validate((val) =>{
                if(val){
                    this.$emit('submit')
                }
            })
        },
        openWindow(value){
            // console.log('title',value);
            this.title=value;
            this.addDialogVisible=true;
        }
    }
}
</script>




