<style lang="scss" scoped>

</style>
<template>
  <page-box>
        <div class="ui-search-box" slot="search-form">
            <el-form class="ui-search-form"  label-width="120px">
                <el-form-item label="名称">
                    <el-input size="mini" class="ui-item-reset" v-model="params.username" clearable></el-input>
                </el-form-item>
                <el-form-item label="手机">
                    <el-input size="mini" class="ui-item-reset" v-model="params.phone" clearable></el-input>
                </el-form-item>
                <el-form-item label="邮箱">
                    <el-input size="mini" class="ui-item-reset" v-model="params.email" clearable></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-select placeholder="请选择" size="mini" class="ui-item-reset" v-model="params.sex" clearable>
                        <el-option label="男" value="1"></el-option>
                        <el-option label="女" value="2"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div class="ui-search-btus">
                <el-button type="primary" size="mini" @click="getData">搜索</el-button>
                <!-- <el-button size="mini">重置</el-button> -->
            </div>
        </div>
        <div class="ui-list ui-box" slot="list-box">
            <div class="box-header ui-list-header">
                <div>用户列表</div>
                <div>
                    <el-button type="primary" size="mini" @click="exportExcel">导出</el-button>
                    <el-button type="primary" size="mini" @click="open">新增</el-button>
                </div>
            </div>
            <el-table :data="dataList" @selection-change="handleSelectionChange" :cell-style="cellStyle" :header-cell-style="rowClass">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="userId" label="用户id"></el-table-column>
                <el-table-column prop="username" label="用户名"></el-table-column>
                <el-table-column prop="email" label="邮箱"></el-table-column>
                <el-table-column prop="sex" label="性别">
                    <template slot-scope="scope">
                        <span v-if="scope.row.sex==1">男</span>
                        <span v-else>女</span>
                    </template>
                </el-table-column>               
                <el-table-column prop="status" label="状态">
                    <template slot-scope="scope">
                        <el-button type="success" icon="el-icon-check" circle size="mini" v-if="scope.row.status==1"></el-button>
                        <el-button type="danger" icon="el-icon-close" circle v-else size="mini"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="createTime" label="创建时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="updateTime" label="更新时间" show-overflow-tooltip></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                            <el-button size="mini" @click="checkDetail(scope.row)">详情</el-button>
                            <el-button size="mini" type="danger" @click="deleteItem(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <add-item v-bind:userItem.sync='userItem' @submit="addItem" ref="addPerm"></add-item>
        </div>
  </page-box>
</template>
<script>
import pageBox from '@/components/pageBox'
import addItem from '@/components/user/addItem.vue'
export default {
    name: 'roleList',
    components:{
        pageBox,addItem
    },
    data(){
        return{
            dataList: [],
            exportList: [],
            test:'1',
            userItem:{
                username: '',
                password: '',
                email: '',
                phone: '',
                sex: '1',
                age: '',
                status: '1',  
            },
            params:{
                username: '',
                email: '',
                phone: '',
                sex: '',
            }
        }
    },
    created(){
        this.getData()
    },
    methods:{
        getData(){
            post('/pms_api/user/list',this.params).then(res => {
                this.dataList=res.rows
            }).catch(err => {
                console.log(err)
            })
        },
        addItem(){
            post('/pms_api/user/add',this.userItem).then(res => {
                this.$refs.addPerm.addDialogVisible=false;
                console.log('res',res);
                this.getData();
            }).catch(err => {
                console.log(err)
            })
            this.$refs.addPerm.addDialogVisible=false;
        },
        open(){
            // this.addDialogVisible=!this.addDialogVisible
            this.$refs.addPerm.openWindow('新增用户')
        },
        checkDetail(item){
            this.$router.push({
                path: '/userDetail',
                query:{
                    title: item.name,
                    id: item.userId
                }
            })
        },
        deleteItem(item){
            post('/pms_api/user/delete',{'userId':item.userId}).then(res => {
                this.getData()
            }).catch(err => {
                console.log(err)
            })
        },
        handleSelectionChange(val){
            this.exportList=val
        },
        exportExcel(){
            let sexName = {2:'女',1:'男'};
            if(this.exportList.length > 0){
                this.exportList.forEach(item => {
                    item.sexName = sexName[item.sex]
                });
                const tHeader = ['ID','用户id','用户名','性别','年龄','邮箱','手机号','创建时间','修改时间','状态','说明','密钥'];
                const filterVal = ['id','userId','username','sexname','age','email','phone','createTime','updateTime','status','description','salt'];
                const list = this.exportList;
                this.$getGlobal.exportFun('用户列表',tHeader,filterVal,list);
            } else {
                this.$message.error('请先选择需要导出的列表项');
            }
        },
        //表格居中
        cellStyle({row, colum, rowIndex, columIndex}){
            return "text-align: center";
        },
        rowClass({row, rowIndex}){
            return "text-align: center";
        }
    }
}
</script>





